<template>
  <div class="main">
    <div class="content-wrap">
      <h2>学生管理</h2>
      <template v-if="isAdmin">
        <el-card style="margin-bottom:20px">
          <div>
            班级
            <el-select v-model="clazz" @change="getClazz" placeholder="请选择">
              <el-option v-for="item in classes" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
            <el-input
              v-model="searchText"
              placeholder="搜索"
              style="width:250px; padding-left:20px"
              @input="searchName(searchText,1,pageSize)"
              clearable
            ></el-input>
          </div>
        </el-card>
      </template>

      <el-card>
        <div class="button-wrapper">
          <el-button @click="createNewStudent()" style="float:right">
            <i class="el-icon-plus"></i>添加
          </el-button>
        </div>
        <div class="table-wrap">
          <el-table :data="tableData">
            <el-table-column type="index"></el-table-column>
            <el-table-column prop="name" label="姓名" width="180"></el-table-column>
            <el-table-column prop="age" label="年龄" width="100"></el-table-column>
            <el-table-column prop="gender" label="性别" width="100">
              <template slot-scope="scope">{{ checkGender(scope.row.gender) }}</template>
            </el-table-column>
            <el-table-column prop="sclass" label="班级" width="100">
              <template slot-scope="scope">{{getClassName(scope.row.sclass)}}</template>
            </el-table-column>
            <el-table-column prop="birthDate" label="出生日期" width="100"></el-table-column>
            <el-table-column label="操作" width="180">
              <template slot-scope="scope">
                <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="block">
          <el-pagination
            layout="prev,pager,next"
            background
            @current-change="currentChange"
            :current-page="currentPage"
            :total="total"
            :page-size="pageSize"
          ></el-pagination>
        </div>
      </el-card>

      <el-dialog
        :visible.sync="dialogFormShow"
        :title="titleMap[dialogStatus]"
        width="500px"
        center
      >
        <el-form
          ref="dialog"
          :model="dialog"
          label-position="left"
          label-width="80px"
          :rules="checkRules()"
          size="small"
        >
          <el-form-item label="姓名" prop="name" :inline="true" style="width:283px">
            <el-input v-model="dialog.name" id="nameInput"></el-input>
          </el-form-item>

          <el-form-item label="性别" prop="gender">
            <el-select v-model="dialog.gender" placeholder="请选择">
              <el-option
                v-for="item in genders"
                :key="item.id"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="班级" prop="sclass">
            <el-select v-model="dialog.sclass" placeholder="请选择" :disabled="!isAdmin">
              <el-option v-for="item in classes" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="出生日期" prop="birthDate">
            <el-date-picker
              v-model="dialog.birthDate"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期"
            ></el-date-picker>
          </el-form-item>
        </el-form>
        <div slot="footer">
          <el-button
            type="primary"
            @click="dialogStatus==='create'?handleNewStudent():handleUpdate()"
          >提交</el-button>
          <el-button type="primary" @click="dialogFormShow=false">取消</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import {
  deleteStudent,
  addStudent,
  updateStudent,
  getStudentByPage,
  searchStudentByName
} from "@/api/student";
//import { getAllGrade } from "@/api/grade";
import store from "@/store/index";

export default {
  name: "StudentManage",
  data() {
    return {
      total: 0,
      pageSize: 5,
      currentPage: 1,

      searchText: "",
      searchFlag: false,

      roles: store.getters.roles,

      tableData: [],
      dialog: {
        id: undefined,
        name: "",
        age: undefined,
        gender: undefined,
        sclass: undefined,
        birthDate: undefined
      },

      genders: [
        {
          value: 0,
          label: "男"
        },
        {
          value: 1,
          label: "女"
        }
      ],

      titleMap: {
        create: "添加新学生",
        edit: "修改"
      },

      dialogStatus: "",
      dialogFormShow: false,
      clazz: store.getters.clazz,
      classes: [...store.getters.classes],

      rules_edit: {
        name: [{ required: true, message: "请输入姓名", trigger: "change" }],
        sclass: [{ required: true, message: "请选择班级", trigger: "change" }],
        gender: [{ required: true, message: "请选择性别", trigger: "change" }],
        birthDate: [
          { required: true, message: "请选择日期", trigger: "change" }
        ]
      },
      rules_create: {
        name: [{ required: true, message: "请输入姓名", trigger: "blur" }],
        gender: [{ required: true, message: "请选择性别", trigger: "change" }],
        sclass: [{ required: true, message: "请选择班级", trigger: "change" }],
        birthDate: [
          { required: true, message: "请选择日期", trigger: "change" }
        ]
      }
    };
  },

  created() {
    this.initStudent();
    //this.loadClassInfo();
  },

  watch: {
    searchText: function() {
      if (this.searchText === "") {
        this.searchFlag = false;
      }
    }
  },

  computed: {
    isAdmin: function() {
      return store.getters.roles.some(role => role.role_name === "ROLE_ADMIN");
    }
  },

  methods: {
    initStudent() {
      if (this.clazz === -2 && !this.$route.params.clazz) {
        this.clazz = this.classes.filter(clazz => clazz.name === "1班")[0].id;
      } else if (this.$route.params.clazz) {
        this.clazz = Number(this.$route.params.clazz);
      }

      this.getStudentApi();

      //store.dispatch('grade/getClasses')
    },

    getClazz(classId) {
      this.currentPage = 1;
      this.clazz = classId;
      this.getStudentApi();
    },

    getStudentApi() {
      getStudentByPage(this.clazz, this.currentPage, this.pageSize)
        .then(resp => {
          if (resp) {
            this.tableData = resp.data.obj.data;
            this.total = resp.data.obj.total;
          }
        })
        .catch(error => {
          if (error.response) {
            if (error.response.status === 401) {
              this.$message.error("你无权访问！");
            } else {
              this.$message.error("内部错误");
            }
          } else {
            this.$message.error("内部错误");
          }
        });
    },

    getClassName(clazzId) {
      for (var i in this.classes) {
        if (this.classes[i].id == clazzId) {
          return this.classes[i].name;
        }
      }
    },

    checkGender(gender) {
      if (gender === 0) {
        return "男";
      } else {
        return "女";
      }
    },

    currentChange(page) {
      this.currentPage = page;
      if (this.searchFlag) {
        this.searchName(this.searchText, this.currentPage, this.pageSize);
      } else {
        this.getStudentApi();
      }
    },

    checkRules() {
      if (this.dialogStatus === "edit") {
        return this.rules_edit;
      } else {
        return this.rules_create;
      }
    },

    handleDelete(row) {
      this.$confirm("是否删除", "提示", {
        comfirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(() => {
          deleteStudent(row.id).then(response => {
            if (response.status === 200) {
              this.$message({
                type: "success",
                message: "删除成功"
              });
              const index = this.tableData.indexOf(row);
              this.tableData.splice(index, 1);
              if (this.tableData.length == 0) {
                this.currentPage = this.currentPage - 1;
                this.getStudentApi();
              }
            }
          });
        })
        .catch(() => {
          return;
        });
    },

    handleEdit(row) {
      this.dialogStatus = "edit";
      this.dialogFormShow = true;
      this.dialog = Object.assign({}, row);
      this.dialog.birthDate = new Date(row.birthDate);
      this.$nextTick(() => {
        this.$refs["dialog"].clearValidate();
      });
    },

    handleUpdate() {
      this.$refs["dialog"].validate(valid => {
        if (valid) {
          const dialogData = Object.assign({}, this.dialog);
          if (typeof dialogData.birthDate != "string") {
            dialogData.birthDate = dialogData.birthDate
              .toISOString()
              .split("T")[0];
          }
          updateStudent(dialogData)
            .then(response => {
              if (response.status == 200) {
                dialogData.age =
                  new Date().getFullYear() -
                  +dialogData.birthDate.split("-")[0];
                for (const v of this.tableData) {
                  if (v.id === this.dialog.id) {
                    const index = this.tableData.indexOf(v);
                    if (dialogData.sclass === this.clazz) {
                      this.tableData.splice(index, 1, dialogData);
                    } else {
                      this.tableData.splice(index, 1);
                    }
                    break;
                  }
                }
                this.dialogFormShow = false;
                this.$message({
                  type: "success",
                  message: "修改成功"
                });
              }
            })
            .catch(() => {
              this.dialogFormShow = false;
              this.$message.error("修改失败");
            });
        } else {
          return false;
        }
      });
    },

    createNewStudent() {
      this.dialogStatus = "create";
      this.dialogFormShow = true;
      if (this.dialog.name) {
        this.dialog = {};
      }
      if (!this.isAdmin) {
        this.dialog.sclass = this.clazz;
      }
      this.$nextTick(() => {
        this.$refs["dialog"].clearValidate();
      });
    },

    handleNewStudent() {
      this.$refs["dialog"].validate(valid => {
        if (valid) {
          addStudent(this.dialog)
            .then(response => {
              if (response.status == 200) {
                this.dialog.age =
                  new Date().getFullYear() -
                  +this.dialog.birthDate.split("-")[0];
                if (this.dialog.sclass === this.clazz) {
                  if (this.total % this.pageSize === 0) {
                    this.total += 1;
                  }
                  this.dialog.id = response.data.obj;
                  this.tableData.unshift(this.dialog);
                }
                this.dialogFormShow = false;
                this.$message({
                  type: "success",
                  message: "添加成功"
                });
              }
            })
            .catch(() => {
              this.dialogFormShow = false;
              this.$message.error("添加失败");
              //console.log(error);
            });
        } else {
          return false;
        }
      });
    },

    searchName(name, page, size) {
      this.searchFlag = true;
      if (page === 1) {
        this.currentPage = 1;
      }
      if (name !== "") {
        searchStudentByName(name, page, size).then(response => {
          this.tableData = response.data.obj.data;
          this.total = response.data.obj.total;
        });
      } else {
        this.initStudent();
      }
    },

    clearSearch() {
      this.searchFlag = false;
      this.initStudent();
    }

    //   loadClassInfo() {
    //     getAllGrade().then(response => {
    //       this.classes = response.data;
    //     });
    //   }
    // }
  }
};
</script>

